const state = {
	city:'眉山'
}

const mutations = {
	'changeCity' (state, citys) {
		state.city = citys
	}

}

const actions = {
	change({commit}, item){
		commit('changeCity',item)
	}
}


const getters = {
	city(state){
		return state.city
	}
}

 export default({
    state,
    mutations,
    actions,
    getters
})